import {ChangeDetectorRef,Component,Inject,OnDestroy,OnInit,PLATFORM_ID,TemplateRef,ViewChild,
} from "@angular/core";
import { FormBuilder, FormControl, Validators } from "@angular/forms";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { Router } from "express";
import { TallerService } from "../../taller.service";
import { Subject, takeUntil } from "rxjs";
import { environment } from "src/environments/environment";
import { isPlatformBrowser } from "@angular/common";

@Component({
  selector: "app-ver-observacion-taller",
  templateUrl: "ver-observacion.component.html",
  styleUrls: ["./ver-observacion.component.scss"],
})
export class VerObservacionComponent implements OnInit, OnDestroy {

  @ViewChild("modalVerObservacion", { static: false })ModalVerObservacion: TemplateRef<any>;
  private _unsubscribeAll: Subject<any> = new Subject<any>();

  public closeResult: string;
  public modalOpen: boolean = false;

  loading: boolean = true;

  acepting: boolean = true;

  Toast: any;

  entrada: any = {};

  vehiculo:any = {};

  searchPlacaControl: FormControl = new FormControl("");

  showInfo: boolean = false;

  observacionControl: FormControl = new FormControl("", Validators.required);

  limit = environment.pagination_limit;

  constructor(
    @Inject(PLATFORM_ID) private platformId: Object,
    // private router: Router,
    private modalService: NgbModal,
    private _formBuilder: FormBuilder,
    private _tallerService: TallerService,
    private _changeDetectorRef: ChangeDetectorRef
  ) {}

  ngOnInit() {
    this._tallerService.entradaSelected$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((entrada: any) => {
        this.loading = true;
        // Update the entrada list
        if(entrada !== null){
            this.entrada = entrada;
          this.obtenerVehiculoPorPlaca(entrada.placa);
        }
        // console.log(this.manilla);

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });
  }

  ngOnDestroy() {
    if(this.modalOpen){
      this.modalService.dismissAll();
    }

    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }

  openModal() {
    this.loading = false;
    this.searchPlacaControl.enable();
    this.searchPlacaControl.setValue("");
    this.acepting = true;
    this.vehiculo = {};
    this.modalOpen = true;
    this.showInfo = false;
    if (isPlatformBrowser(this.platformId)) {
      // For SSR
      this.modalService
        .open(this.ModalVerObservacion, {
          size: "lg",
          ariaLabelledBy: "modal-basic-title",
          centered: true,
          windowClass: "ModalVerObservacion",
          backdrop: "static",
          keyboard: false,
          scrollable: true,
        })
        .result.then(
          (result) => {
            `Result ${result}`;
          },
          (reason) => {
            this.closeResult = `Dismissed`;
          }
        );
    }
  }

  obtenerVehiculoPorPlaca(placa:any){
    this._tallerService.obtenerVehiculoPorPlaca(placa).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.vehiculo = response;
        this.loading = false;
        this.showInfo = true;
        // Mark for check
        this._changeDetectorRef.markForCheck();
      },(error) => {
        this.loading = false;
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }
}
