import { HttpClient, HttpParams } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { BehaviorSubject, Observable, tap } from "rxjs";
import { environment } from "src/environments/environment";

@Injectable({
  providedIn: "root",
})
export class TallerService {
  private url: string = `${environment.urlWeb}`;
  private _entradasList: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  private _entradaSelected: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  // -----------------------------------------------------------------------------------------------------
  // @ Accessors
  // -----------------------------------------------------------------------------------------------------

  /**
   * Getter for entradas list
   */
  get entradasList$(): Observable<any[]> {
    return this._entradasList.asObservable();
  }

   /**
   * Getter for entradas Selected
   */
   get entradaSelected$(): Observable<any[]> {
    return this._entradaSelected.asObservable();
  }

  /**
   * Constructor
   */
  constructor(private _httpClient: HttpClient) {}

  // -----------------------------------------------------------------------------------------------------
  // @ metodos manillas list
  // -----------------------------------------------------------------------------------------------------

    //   getManillasList(offset: any, limit: any, tipo:any, estado:any): Observable<any> {
    //     let params = new HttpParams();
    //     params = params.set("offset", offset);
    //     params = params.set("limit", limit);
    //     if(tipo !== null){ 
    //       params = params.set("tipo", tipo)
    //     };
    //     if(estado !== null){ 
    //       params = params.set("estado", estado)
    //     };
    //     return this._httpClient
    //       .get<any[]>(`${this.url}manillas/findAll`, { params })
    //       .pipe(
    //         tap((response: any) => {
    //           this._manillasList.next(response);
    //         })
    //       );
    //   }

  obtenerVehiculoPorPlaca(placa:any): Observable<any> {
    return this._httpClient.get<any[]>(`${this.url}manillas/ObtenerInfoMotoPorPlaca/${placa}`);
  }

  crearObservacionVehiculoPorPlaca(placa:any, data:any): Observable<any> {
    return this._httpClient.post<any[]>(`${this.url}manillas/crearEntrada/${placa}`, data);
  }

  getObservacionesTaller(offset: any, limit: any, placa:any): Observable<any> {
    let params = new HttpParams();
    params = params.set("offset", offset);
    params = params.set("limit", limit);
    if(placa !== null){ 
      params = params.set("placa", placa)
    };
    return this._httpClient.get<any[]>(`${this.url}entradas/MisEntradas`, { params }).pipe(
        tap((response: any) => {
          this._entradasList.next(response);
        })
      );
  }

  getObservacionesTallerUpdated(offset: any, limit: any, placa:any): Observable<any> {
    let params = new HttpParams();
    params = params.set("offset", offset);
    params = params.set("limit", limit);
    if(placa !== null){ 
      params = params.set("placa", placa)
    };
    return this._httpClient.get<any[]>(`${this.url}entradas/MisEntradas`, { params });
  }

  updateObservacionesList(list:any){
    this._entradasList.next(list);
  }

  updateEntradaSelected(entrada:any){
    this._entradaSelected.next(entrada);
  }
}
