import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { GalleryModule } from "ng-gallery";
import { LightboxModule } from "ng-gallery/lightbox";
import { SharedModule } from "../shared/shared.module";
import { TallerRoutingModule } from "./taller-routing.module";
import { ModalContraseniaComponent } from "./dashboard/modal-contrasenia/modal-contrasenia.component";
import { DashboardTallerComponent } from "./dashboard/dashboard-taller.component";
import { ModalObservacionComponent } from "./dashboard/modal-observacion/modal-observacion.component";
import {AutosizeModule} from 'ngx-autosize';
import { ObservacionesComponent } from "./observaciones/observaciones.component";
import { VerObservacionComponent } from "./modals/ver-observacion/ver-observacion.component";
import { NgxMaskDirective, NgxMaskPipe, provideNgxMask } from "ngx-mask";

@NgModule({
    declarations: [
      ModalContraseniaComponent,
      DashboardTallerComponent,
      ModalObservacionComponent,
      ObservacionesComponent,
      VerObservacionComponent
    ],
    imports: [
      CommonModule,
      GalleryModule,
      LightboxModule,
      SharedModule,
      TallerRoutingModule,
      AutosizeModule,
      NgxMaskDirective, NgxMaskPipe
    ],
    providers: [provideNgxMask()]
  })
  export class TallerModule { }