import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardTallerComponent } from './dashboard/dashboard-taller.component';
import { ObservacionesComponent } from './observaciones/observaciones.component';
import { TallerObservacionesResolver } from './taller.resolver';

const routes: Routes = [
  { 
    path: 'dashboard', 
    component: DashboardTallerComponent 
  },
  {
    path: 'observaciones', 
    component: ObservacionesComponent,
    resolve: {
      observaciones: TallerObservacionesResolver,
    }
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TallerRoutingModule { }
