import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Subject, takeUntil } from 'rxjs';
import { AuthService } from 'src/app/core/auth/auth.service';
import { UserService } from 'src/app/core/user/user.service';
import { ModalContraseniaComponent } from './modal-contrasenia/modal-contrasenia.component';
import { NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { ModalObservacionComponent } from './modal-observacion/modal-observacion.component';
import { FormControl, Validators } from '@angular/forms';
import Swal from 'sweetalert2';
import { Gallery, GalleryItem, ImageItem, ImageSize, ThumbnailsPosition } from 'ng-gallery';
import { Lightbox } from 'ng-gallery/lightbox';

@Component({
  selector: 'app-dashboard-taller',
  templateUrl: './dashboard-taller.component.html',
  styleUrls: ['./dashboard-taller.component.scss']
})
export class DashboardTallerComponent implements OnInit, OnDestroy {

  @ViewChild("modalContrasenia") ModalContrasenia: ModalContraseniaComponent;
  // @ViewChild("modalObservacion") ModalObservacion: ModalObservacionComponent;

  public openDashboard: boolean = false;

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  user:any = {};

  modalReference: NgbModalRef;
  Toast:any;

  editProfile: boolean = false;

  uploadedFiles:any;

  imageBase64: any = '';

  imgPreview: any = '';

  telefonoInputControl: FormControl = new FormControl('', [Validators.required, Validators.pattern("[0-9]*")]);

  direccionInputControl: FormControl = new FormControl('', Validators.required);

  fotoInputControl: FormControl = new FormControl('');

  disableButton: boolean = false;

  items: GalleryItem[];

  constructor(
    private _authService: AuthService,
    private _userService: UserService,
    private _changeDetectorRef: ChangeDetectorRef, public gallery: Gallery, public lightbox: Lightbox
  ) {
    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    })
   }

  ngOnInit(): void {
    // Subscribe to user changes
    this._userService.user$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe((user: any) => {
        if(user.fotoBase64){
          this.items = [new ImageItem({ src: user.fotoBase64, thumb: user.fotoBase64 })];
          // this.items = [new ImageItem({ src: this.imagenTemp, thumb: this.imagenTemp })];
          const lightboxRef = this.gallery.ref('lightbox');
          // Add custom gallery config to the lightbox (optional)
          lightboxRef.setConfig({
            imageSize: ImageSize.Cover,
            thumbPosition: ThumbnailsPosition.Top
          });

          // Load items into the lightbox gallery ref
          lightboxRef.load(this.items);
        }
        this.user = user;

        // Mark for check
        this._changeDetectorRef.markForCheck();
    });
  }

  /**
     * On destroy
     */
  ngOnDestroy(): void
  {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next(null);
      this._unsubscribeAll.complete();
  }

  ToggleDashboard() {
    this.openDashboard = !this.openDashboard;
  }

  // openModalObservacion(){
  //   this.modalReference = this.modalService.open(ModalObservacionComponent, {backdrop: 'static',size: 'lg', keyboard: false, centered: true});
  // }

  btnEditProfile(){
    this.editProfile = !this.editProfile;
    if(this.user.fotoBase64 !== '' && this.user.fotoBase64 !== undefined){
      this.imgPreview = this.user.fotoBase64;
    }else{
      this.imgPreview = '';
    }
    this.telefonoInputControl.setValue(this.user.telefono);
    this.direccionInputControl.setValue(this.user.direccion);
    this.fotoInputControl.setValue('');
    this.imageBase64 = '';
    // Mark for check
    this._changeDetectorRef.markForCheck();
  }
  
  onFileSelected(event){
    this.uploadedFiles = event.target.files[0];
    if(this.uploadedFiles.type === 'image/jpeg' || this.uploadedFiles.type === 'image/png' || this.uploadedFiles.type === 'image/jpg'){
      const reader = new FileReader();
      reader.readAsDataURL(this.uploadedFiles);
      reader.onload = () => {
        let dataN = reader.result.toString();
        let dataD = '';
        dataD = dataN.replace(/^data:image\/\w+;base64,/,'');
        this.imageBase64 = dataD;
        this.imgPreview = dataN;
      };
    }else{
      this.Toast.fire({
        icon: "error",
        title: 'Debe subir un tipo de imagen valido (JPEG, JPG, PNG).',
      });
      this.fotoInputControl.setValue('');
      this.uploadedFiles = '';
    }
  }

  btnGuardarPerfil(){
    if(this.telefonoInputControl.valid && this.direccionInputControl.valid){
      this.disableButton = true;
      let data:any = {};
      data.telefono = this.telefonoInputControl.value;
      data.direccion = this.direccionInputControl.value;
      if(this.imageBase64 !== ''){
        data.fotoBase64 = this.imageBase64;
      }
      this.updatePerfil(data);
    }else{
      this.disableButton = false;
      this.Toast.fire({
        icon: "error",
        title: 'El campo telefono de contacto y Direccion no pueden ser vacios.',
      });
    }
  }

  updatePerfil(data:any){
    this._userService.updateUserById(data, this.user._id).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.Toast.fire({
          icon: "success",
          title: 'El perfil ha sido editado con exito.',
        });
        this.user = response;
        if(this.user.fotoBase64){
          this.items = [new ImageItem({ src: this.user.fotoBase64, thumb: this.user.fotoBase64 })];
          // this.items = [new ImageItem({ src: this.imagenTemp, thumb: this.imagenTemp })];
          const lightboxRef = this.gallery.ref('lightbox');
          // Add custom gallery config to the lightbox (optional)
          lightboxRef.setConfig({
            imageSize: ImageSize.Cover,
            thumbPosition: ThumbnailsPosition.Top
          });

          // Load items into the lightbox gallery ref
          lightboxRef.load(this.items);
        }
        this.editProfile = false;
        this.disableButton = false;
        // Mark for check
        this._changeDetectorRef.markForCheck();
      },(error) => {
        this.disableButton = false;
        this.Toast.fire({
          icon: "error",
          title: error.error.message,
        });
      }
    );
  }
}
