import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { TallerService } from './taller.service';
import { environment } from 'src/environments/environment';

let limit = environment.pagination_limit;

@Injectable({
    providedIn: 'root'
})
export class TallerObservacionesResolver implements Resolve<any>
{
    /**
     * Constructor
     */
    constructor(private _tallerService: TallerService)
    {
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Public methods
    // -----------------------------------------------------------------------------------------------------

    /**
     * Resolver
     *
     * @param route
     * @param state
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]>
    {
        return this._tallerService.getObservacionesTaller(0,limit, null);
    }
}