import { isPlatformBrowser } from "@angular/common";
import { ChangeDetectorRef, Component, Inject, Input, OnDestroy, OnInit, PLATFORM_ID, TemplateRef, ViewChild } from "@angular/core";
import { AbstractControl, FormBuilder, FormGroup, NgForm, ValidationErrors, ValidatorFn, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { ModalDismissReasons, NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { Subject, takeUntil } from "rxjs";
import { UserService } from "src/app/core/user/user.service";
import Swal from "sweetalert2";

@Component({
  selector: "app-modal-contrasenia-taller",
  templateUrl: "./modal-contrasenia.component.html",
  styleUrls: ["./modal-contrasenia.component.scss"],
})
export class ModalContraseniaComponent implements OnInit, OnDestroy {

    @Input() user: any;
    @ViewChild("modalContrasenia", { static: false }) ModalContrasenia: TemplateRef<any>;
    @ViewChild("passwordNgForm") passwordNgForm: NgForm;

    public closeResult: string;
    public modalOpen: boolean = false;

    passwordForm: FormGroup;

    Toast:any;

    disableButtons:boolean = false;

    private _unsubscribeAll: Subject<any> = new Subject<any>();

    constructor(
        @Inject(PLATFORM_ID) private platformId: Object,
        private router: Router, private modalService: NgbModal,
        private _formBuilder: FormBuilder, private _userService: UserService,
        private _changeDetectorRef: ChangeDetectorRef,) {
          this.Toast = Swal.mixin({
            toast: true,
            position: 'top-end',
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener('mouseenter', Swal.stopTimer)
              toast.addEventListener('mouseleave', Swal.resumeTimer)
            }
          })
    }

    static patternValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
        return (control: AbstractControl): { [key: string]: any } => {
          if (!control.value) {
            // if control is empty return no error
            return null;
          }
    
          // test the value of the control against the regexp supplied
          const valid = regex.test(control.value);
    
          // if true, return no error (no error), else return error passed in the second parameter
          return valid ? null : error;
        };
    }

    ngOnInit(): void {
        this.passwordForm = this._formBuilder.group({
            oldpassword  : ['', Validators.compose([
                Validators.required,
                Validators.minLength(8)
            ])],
            newPassword      : ['', Validators.compose([
                Validators.required,
                ModalContraseniaComponent.patternValidator(/\d/, { hasNumber: true }),
                ModalContraseniaComponent.patternValidator(/[A-Z]/, { hasCapitalCase: true }),
                ModalContraseniaComponent.patternValidator(/[a-z]/, { hasSmallCase: true }),
                ModalContraseniaComponent.patternValidator(/[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/, { hasSpecialCharacters: true }),
                Validators.minLength(8)
            ])],
        });
    }

    openModal() {
        this.modalOpen = true;
        if (isPlatformBrowser(this.platformId)) { // For SSR 
          this.modalService.open(this.ModalContrasenia, { 
            size: 'md',
            ariaLabelledBy: 'modal-basic-title',
            centered: true,
            windowClass: 'ModalContrasenia',
            keyboard: false,
            backdrop: 'static',
          }).result.then((result) => {
            `Result ${result}`
          }, (reason) => {
            this.closeResult = `Dismissed`;
          });
        }
    }

    private getDismissReason(reason: any) {
    }

    ngOnDestroy() {
        if(this.modalOpen){
          this.modalService.dismissAll();
        }

        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next(null);
        this._unsubscribeAll.complete();
    }

    submit(): void{
      if(this.passwordForm.valid){
        this.disableButtons = true;
        this.cambiarContrase√±a();
      }else{
        this.Toast.fire({
          icon: "error",
          title: 'Debes completar los campos antes de cambiar la contrase√±a',
        });
      }
      // Mark for check
      this._changeDetectorRef.markForCheck();
  }

    cambiarContrase√±a(){
      if(this.passwordForm.invalid){
        return;
      }

      // Disable the form
      this.passwordForm.disable();
      let data = this.passwordForm.getRawValue();
      
      this._userService.updatePasswordById(data).pipe(takeUntil(this._unsubscribeAll)).subscribe(
        (response:any) => {
          this.Toast.fire({
            icon: "success",
            title: 'La contrase√±a ha sido actualizada con exito.',
          });
          this.disableButtons = false;
          // enable the form
          this.passwordForm.enable();
          this.modalService.dismissAll();
          // Mark for check
          this._changeDetectorRef.markForCheck();
        },(error) => {
          this.Toast.fire({
            icon: "error",
            title: error.error.message,
          });
          this.disableButtons = false;
          // enable the form
          this.passwordForm.enable();
          // Mark for check
          this._changeDetectorRef.markForCheck();
        }
      );
    }
}
