import { ViewportScroller } from "@angular/common";
import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild } from "@angular/core";
import { FormBuilder } from "@angular/forms";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { Subject, takeUntil } from "rxjs";
import { environment } from "src/environments/environment";
import Swal from "sweetalert2";
import { TallerService } from "../taller.service";
import { UserService } from "src/app/core/user/user.service";
import { ModalObservacionComponent } from "../dashboard/modal-observacion/modal-observacion.component";
import { VerObservacionComponent } from "../modals/ver-observacion/ver-observacion.component";

@Component({
  selector: "app-observaciones-taller",
  templateUrl: "observaciones.component.html",
  styleUrls: ["./observaciones.component.scss"],
})
export class ObservacionesComponent implements OnInit, OnDestroy {

  private _unsubscribeAll: Subject<any> = new Subject<any>();
  @ViewChild("modalObservacion") ModalObservacion: ModalObservacionComponent;
  @ViewChild("verObservacionModal") verObservacionModal: VerObservacionComponent;

  Toast:any;

  entradasCount: number = 0;
  entradasList: any = [];
  page: any = 1;

  user:any = {};

  entradaSelected:any = {};
  limit: any = environment.pagination_limit;

  disableButtons: boolean = false;

  constructor(
    private _tallerService: TallerService,
    private _changeDetectorRef: ChangeDetectorRef,
    private modalService: NgbModal,
    private _formBuilder: FormBuilder,
    private viewScroller: ViewportScroller,
    private _userService: UserService,
  ) {
    this.Toast = Swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener('mouseenter', Swal.stopTimer)
          toast.addEventListener('mouseleave', Swal.resumeTimer)
        }
      })
  }

  ngOnInit() {
    this._tallerService.entradasList$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((entradas: any) => {
        // Update the entradasList
        this.entradasList = entradas.entradas;
        this.entradasCount = entradas.totalDocuments;
        // Mark for check
        this._changeDetectorRef.markForCheck();
      });

    // Subscribe to user changes
    this._userService.user$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe((user: any) => {
        this.user = user;

        // Mark for check
        this._changeDetectorRef.markForCheck();
    });
  }

  /**
   * On destroy
   */
  ngOnDestroy(): void {
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }

  abrirModalVerObservacion(observacion: any){
    this._tallerService.updateEntradaSelected(observacion);
    this.verObservacionModal.openModal();
  }

  onPageChange(event:any){
    this.getObservacionesListUpdated(((event - 1) * this.limit), null);
  }

  getObservacionesListUpdated(page:any, placa:any){
    this._tallerService.getObservacionesTallerUpdated(page, this.limit, placa).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this._tallerService.updateObservacionesList(response);
      },(error) => {
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }
}
