import { isPlatformBrowser } from "@angular/common";
import { ChangeDetectorRef, Component, Inject, Input, OnDestroy, OnInit, PLATFORM_ID, TemplateRef, ViewChild } from "@angular/core";
import { AbstractControl, FormBuilder, FormControl, FormGroup, NgForm, ValidationErrors, ValidatorFn, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { ModalDismissReasons, NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { Subject, takeUntil } from "rxjs";
import { TallerService } from "../../taller.service";
import Swal from "sweetalert2";
import { environment } from "src/environments/environment";

@Component({
  selector: "app-modal-observacion-taller",
  templateUrl: "./modal-observacion.component.html",
  styleUrls: ["./modal-observacion.component.scss"],
})
export class ModalObservacionComponent implements OnInit, OnDestroy {

    @ViewChild("modalObservacion", { static: false }) ModalObservacion: TemplateRef<any>;
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    public closeResult: string;
    public modalOpen: boolean = false;

    loading: boolean = false;

    acepting: boolean = true;

    Toast:any;

    vehiculo: any = {};

    searchPlacaControl: FormControl = new FormControl('');

    showInfo: boolean = false;

    observacionControl: FormControl = new FormControl('', Validators.required);

    limit = environment.pagination_limit;

    constructor(
        @Inject(PLATFORM_ID) private platformId: Object,
        private router: Router, private modalService: NgbModal,
        private _formBuilder: FormBuilder,
        private _tallerService: TallerService,
        private _changeDetectorRef: ChangeDetectorRef,) {

          this.Toast = Swal.mixin({
            toast: true,
            position: 'top-end',
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener('mouseenter', Swal.stopTimer)
              toast.addEventListener('mouseleave', Swal.resumeTimer)
            }
          });
        
    }

   
    ngOnInit(): void {
    }

    ngOnDestroy() {
      if(this.modalOpen){
        this.modalService.dismissAll();
      }

      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next(null);
      this._unsubscribeAll.complete();
    }

    openModal() {
        this.loading = false;
        this.searchPlacaControl.enable();
        this.searchPlacaControl.setValue('');
        this.acepting = true;
        this.vehiculo = {};
        this.modalOpen = true;
        this.showInfo = false;
        if (isPlatformBrowser(this.platformId)) { // For SSR 
          this.modalService.open(this.ModalObservacion, { 
            size: 'lg',
            ariaLabelledBy: 'modal-basic-title',
            centered: true,
            windowClass: 'ModalObservacion' ,
            backdrop: 'static',
            keyboard: false,
            scrollable: true
          }).result.then((result) => {
            `Result ${result}`
          }, (reason) => {
            this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
          });
        }
    }

    private getDismissReason(reason: any): string {
        if (reason === ModalDismissReasons.ESC) {
          return 'by pressing ESC';
        } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
          return 'by clicking on a backdrop';
        } else {
          return `with: ${reason}`;
        }
    }

    btnBuscarVehiculoPlaca(){
      this.showInfo = false;
      this.loading = true;
      let placa = this.searchPlacaControl.value;
      // console.log(placa);
      this.obtenerVehiculoPorPlaca(placa);
    }

    obtenerVehiculoPorPlaca(placa:any){
      this._tallerService.obtenerVehiculoPorPlaca(placa).pipe(takeUntil(this._unsubscribeAll)).subscribe(
        (response:any) => {
          this.vehiculo = response;
          this.loading = false;
          this.showInfo = true;
          // Mark for check
          this._changeDetectorRef.markForCheck();
        },(error) => {
          this.loading = false;
          this.Toast.fire({
            icon: 'error',
            title: error.error.message
          });
        }
      );
    }

    btnRealizarObservacion(){
      if(this.observacionControl.invalid){
        this.Toast.fire({
          icon: 'error',
          title: 'Debe llenar el campo de observacion antes de continuar.'
        });
      }else{
        this.realizarObservacion();
      }
    }

    realizarObservacion(){
      // Do nothing if the form is invalid
      if (this.observacionControl.invalid) {
        return;
      }

      this.observacionControl.disable();

      let observaciones = this.observacionControl.value;

      this._tallerService.crearObservacionVehiculoPorPlaca(this.vehiculo.placa,{observaciones}).pipe(takeUntil(this._unsubscribeAll)).subscribe(
        (response:any) => {
          this.Toast.fire({
            icon: 'success',
            title: 'Observacion realizada con exito.'
          });
          this.getObservacionesListUpdated();
          this.modalService.dismissAll();
        },(error) => {
          this.Toast.fire({
            icon: 'error',
            title: error.error.message
          });
        }
      );
    }

    getObservacionesListUpdated(){
      this._tallerService.getObservacionesTallerUpdated(0, this.limit, null).pipe(takeUntil(this._unsubscribeAll)).subscribe(
        (response:any) => {
          this._tallerService.updateObservacionesList(response);
        },(error) => {
          this.Toast.fire({
            icon: 'error',
            title: error.error.message
          });
        }
      );
    }

    submit(): void{
        
    }
}
